import React from "react";
import {
  TabbedForm,
  FormTab,
  TextInput,
  FileInput,
  DateInput,
  BooleanInput,
  NumberInput,
  RadioButtonGroupInput
} from "react-admin";

export const VendorForm = props => (
  <TabbedForm {...props} submitOnEnter={false}>
    <FormTab label="Details">
      <RadioButtonGroupInput
        source="vendor_type"
        choices={[
          { id: "contract", name: "Contract" },
          { id: "single", name: "One Time" }
        ]}
      />
      <TextInput source="name" />
      <TextInput source="email" />
      <TextInput source="phone" />
      <TextInput source="fax" />
      <TextInput source="address" options={{ multiline: true, rows: 3 }} />
    </FormTab>
    <FormTab label="Contact Details">
      <TextInput source="vendor_contact_details.name" label="Name" />
      <TextInput source="vendor_contact_details.email" label="Email" />
      <TextInput source="vendor_contact_details.phone" label="Phone" />
    </FormTab>
    <FormTab label="Finance Contact Details">
      <TextInput source="vendor_finance_contact_details.name" label="Name" />
      <TextInput source="vendor_finance_contact_details.email" label="Email" />
      <TextInput source="vendor_finance_contact_details.phone" label="Phone" />
    </FormTab>
    <FormTab label="Legal Details">
      <TextInput source="trade_license_number" />
      <FileInput source="trade_license_document" />
      <TextInput source="vat_number" />
      <FileInput source="vat_certificate" />
      <FileInput source="certificate_goodstanding" />
    </FormTab>
    <FormTab label="Contract">
      <TextInput
        source="contract.payment_terms"
        label="Payment Terms"
        options={{ multiline: true, rows: 3 }}
      />
      <DateInput source="contract.term.start" label="Term Start" />
      <DateInput source="contract.term.end" label="Term End" />
      <BooleanInput source="contract.cancellable" label="Cancellable" />
      <BooleanInput source="contract.autoRenewable" label="Auto-Renewable" />
      <NumberInput
        source="contract.terminationNotificationPeriod"
        label="Termination Notification Period (days)"
      />
    </FormTab>
  </TabbedForm>
);
export default VendorForm;
