import React from "react";
import { FunctionField } from "react-admin";

const getStatusText = status => {
  switch (status) {
    case "pending":
      return "Awaiting Legal Approval";
    case "legal_accepted":
      return "Awaiting Finance Approval";
    case "finance_accepted":
      return "Approved";
    case "legal_rejected":
      return "Rejected by Legal";
    case "finance_rejected":
      return "Rejected by Finance";
    default:
      return status;
  }
};

export const StatusField = props => {
  return (
    <FunctionField
      {...props}
      label="Status"
      render={record => `${getStatusText(record.status)}`}
    />
  );
};
