import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, crudUpdate, startUndoable } from "react-admin";
import Close from "@material-ui/icons/Close";

class StatusActionComponent extends Component {
  onClick = event => {
    event.stopPropagation();
    const {
      resource,
      basePath,
      record,
      startUndoable,
      nextStatus
    } = this.props;
    startUndoable(
      crudUpdate(
        resource,
        record.id,
        {
          ...record,
          status: nextStatus
        },
        record,
        basePath,
        null,
        true
      )
    );
  };

  render() {
    return (
      <Button label={this.props.label} onClick={this.onClick}>
        <Close />
      </Button>
    );
  }
}

export const StatusAction = connect(
  undefined,
  { startUndoable }
)(StatusActionComponent);
