import React from "react";

import { Admin, Resource } from "react-admin";
import "./App.css";
import { VendorList } from "./VendorList/VendorList";
import VendorCreate from "./vendors/VendorCreate";
import VendorEdit from "./vendors/VendorEdit";
import VendorShow from "./vendors/VendorShow";
import jsonServerProvider from "ra-data-json-server";
import LoginWithTheme from "./Login/Login";
import { Button } from "@material-ui/core";

const dataProvider = jsonServerProvider("http://13.233.125.210:8081");

const App = () => {
  return (
    <Admin
      dataProvider={dataProvider}
      loginPage={LoginWithTheme}
      logoutButton={() => <Button title="Logout" />}
    >
      <Resource
        name="vendors"
        list={VendorList}
        show={VendorShow}
        edit={VendorEdit}
        create={VendorCreate}
      />
    </Admin>
  );
};

export default App;
