import Users from "../data/users";

const getRole = () => {
  if (window.currentUser)
    return (
      (Users.find((user) => user.email === window.currentUser.username) &&
        Users.find((user) => user.email === window.currentUser.username)
          .role) ||
      "user"
    );
  else return "user";
};

export default getRole;
