import React, { Fragment } from "react";
import { TextField, EditButton, FunctionField } from "react-admin";
import withStyles from "@material-ui/core/styles/withStyles";
import Divider from "@material-ui/core/Divider";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import getRole from "../utils/getRole";
import { StatusAction } from "../Actions/StatusAction";
import { StatusField } from "../Fields/StatusField";
import { Datagrid } from "./Datagrid";
const datagridStyles = {
  total: { fontWeight: "bold" }
};

class TabbedDatagrid extends React.Component {
  tabs = [
    { id: "all", name: "all" },
    { id: "pending", name: "Awaiting Approval" },
    { id: "drafts", name: "drafts" }
  ];

  state = { ordered: [], delivered: [], cancelled: [] };

  static getDerivedStateFromProps(props, state) {
    if (props.ids !== state[props.filterValues.tabStatus]) {
      return { ...state, [props.filterValues.tabStatus]: props.ids };
    }
    return null;
  }

  handleChange = (event, value) => {
    const { filterValues, setFilters } = this.props;
    setFilters({ ...filterValues, tabStatus: value });
  };

  render() {
    const { classes, filterValues, ...props } = this.props;
    return (
      <Fragment>
        <Tabs
          fullWidth
          centered
          value={filterValues.tabStatus}
          onChange={this.handleChange}
        >
          {this.tabs
            .filter(
              tab => !(tab.name === "Awaiting Approval" && getRole() === "user")
            )
            .map(choice => (
              <Tab key={choice.id} label={choice.name} value={choice.id} />
            ))}
        </Tabs>
        <Divider />
        {filterValues.tabStatus === "all" && (
          <Datagrid {...props} rowClick="show">
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="vendor_type" />
            <TextField source="email" />
            <TextField source="phone" />
            <TextField source="fax" />
            <TextField source="trade_license_number" label="Trade license" />
            <TextField
              source="vendor_contact_details.name"
              label="Vendor Contact"
            />
            <TextField
              source="vendor_finance_contact_details.name"
              label="Finance Contact"
            />
            {/** pending/legal_accepted/finance_accepted/legal_rejected/finance_rejected */}
            <StatusField label="Status" />
            <EditButton />
          </Datagrid>
        )}
        {filterValues.tabStatus === "pending" && (
          <Datagrid
            {...props}
            status={[getRole() === "legal" ? "pending" : "legal_accepted"]}
            rowClick="show"
          >
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="vendor_type" />
            <TextField source="email" />
            <TextField source="phone" />
            <TextField source="fax" />
            <TextField source="trade_license_number" label="Trade license" />
            <StatusField label="Status" />
            <StatusAction
              label="Approve"
              nextStatus={
                getRole() === "legal" ? "legal_accepted" : "finance_accepted"
              }
            />
            <StatusAction
              label="Reject"
              nextStatus={
                getRole() === "legal" ? "legal_rejected" : "finance_rejected"
              }
            />
            <EditButton />
          </Datagrid>
        )}
        {filterValues.tabStatus === "drafts" && (
          <Datagrid {...props} status={[]} rowClick="show">
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="vendor_type" />
            <FunctionField label="Status" render={() => "Draft"} />
            <EditButton />
          </Datagrid>
        )}
      </Fragment>
    );
  }
}

export const StyledTabbedDatagrid = withStyles(datagridStyles)(TabbedDatagrid);
