import React from "react";
import { Datagrid as RADatagrid } from "react-admin";

export const Datagrid = ({ ids, data, children, status, ...rest }) => {
  return (
    <RADatagrid
      {...rest}
      ids={ids.filter(id => {
        return !status || status.includes(data[id].status);
      })}
      data={Object.keys(data)
        .filter(id => {
          return !status || status.includes(data[id].status);
        })
        .reduce((obj, id) => {
          obj[id] = data[id];
          return obj;
        }, {})}
    >
      {children}
    </RADatagrid>
  );
};
