import React from "react";
import {
  Show,
  TabbedShowLayout,
  Tab,
  TextField,
  FileField,
  DateField,
  BooleanField,
  NumberField
} from "react-admin";

export const VendorForm = props => (
  <Show {...props}>
    <TabbedShowLayout>
      <Tab label="Details">
        <TextField source="vendor_type" />
        <TextField source="name" />
        <TextField source="email" />
        <TextField source="phone" />
        <TextField source="fax" />
        <TextField source="address" options={{ multiline: true, rows: 3 }} />
      </Tab>
      <Tab label="Contact Details">
        <TextField source="vendor_contact_details.name" label="Name" />
        <TextField source="vendor_contact_details.email" label="Email" />
        <TextField source="vendor_contact_details.phone" label="Phone" />
      </Tab>
      <Tab label="Finance Contact Details">
        <TextField source="vendor_finance_contact_details.name" label="Name" />
        <TextField
          source="vendor_finance_contact_details.email"
          label="Email"
        />
        <TextField
          source="vendor_finance_contact_details.phone"
          label="Phone"
        />
      </Tab>
      <Tab label="Legal Details">
        <TextField source="trade_license_number" />
        <FileField source="trade_license_document" />
        <TextField source="vat_number" />
        <FileField source="vat_certificate" />
        <FileField source="certificate_goodstanding" />
      </Tab>
      <Tab label="Contract">
        <TextField
          source="contract.payment_terms"
          label="Payment Terms"
          options={{ multiline: true, rows: 3 }}
        />
        <DateField source="contract.term.start" label="Term Start" />
        <DateField source="contract.term.end" label="Term End" />
        <BooleanField source="contract.cancellable" label="Cancellable" />
        <BooleanField source="contract.autoRenewable" label="Auto-Renewable" />
        <NumberField
          source="contract.terminationNotificationPeriod"
          label="Termination Notification Period (days)"
        />
      </Tab>
    </TabbedShowLayout>
  </Show>
);
export default VendorForm;
