import React from "react";
import { Filter, TextInput } from "react-admin";

const Filters = (props) => (
  <Filter {...props}>
    <TextInput label="Search" source="name" alwaysOn />
    <TextInput label="Name" source="name" defaultValue="amala" />
    <TextInput label="Address" source="address" defaultValue="Hello, World!" />
  </Filter>
);

export default Filters;
